import React from "react";
import { Form } from "react-bootstrap"
import { connect } from "react-redux"
import {
    UPDATE_DEPOT_ACTION,
    GET_DEPOT_SERIES_ACTION, GET_DEPOT_PRODUCT_SERIES_ACTION, GET_DEPOT_CUSTOMER_ACTION, UPDATE_DEPOT_SERIES_ACTION,
} from "../../../redux/actions/actions"


class DepotSelect extends React.Component {
    state = {
        depot: null,
    }

    static getDerivedStateFromProps = (props, state) => {
        if (state.depot === null || props.depot === undefined) {
            state.depot = 0
            return state
        } else {
            state.depot = parseInt(props.depot)
            return state
        }
        return null

    }

    handleDepotChange = async (index) => {
        let id = this.props.depots[parseInt(index)].id
        await this.props.UPDATE_DEPOT_SERIES_ACTION()
        await this.props.UPDATE_DEPOT_ACTION(index)
        await this.props.GET_DEPOT_SERIES_ACTION(id, this.props.selection_dates.startDate, this.props.selection_dates.endDate)
        await this.props.GET_DEPOT_PRODUCT_SERIES_ACTION(id, this.props.selection_dates.startDate, this.props.selection_dates.endDate)
        await this.props.GET_DEPOT_CUSTOMER_ACTION(id, this.props.depot_customer_year, this.props.depot_customer_month)
    }

    render() {
        const depots = this.props.depots

        return (
            <div className="pie-select">
                <Form.Group className="mb-4 pie-select__child" controlId="exampleForm.ControlInput1">
                    <Form.Label>Depot</Form.Label>
                    <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleDepotChange(e.target.value)}>
                        <option value={this.state.depot}>{depots[parseInt(this.state.depot)].name}</option>
                        {depots.map((depot, idx) =>
                            this.state.depot === idx ? null : <option key={idx} value={idx}>{depot.name}</option>
                        )}

                    </Form.Select>
                </Form.Group>

            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        depots: state.list_reducer.depot_list,
        depot: state.update_reducer.depot,
        selection_dates: state.update_reducer.selection_dates,
        depot_customer_year: state.update_reducer.depot_customer_year,
        depot_customer_month: state.update_reducer.depot_customer_month,
    };
};

const mapDispatchToProps = {
    UPDATE_DEPOT_ACTION,
    GET_DEPOT_SERIES_ACTION, GET_DEPOT_PRODUCT_SERIES_ACTION, GET_DEPOT_CUSTOMER_ACTION,
    UPDATE_DEPOT_SERIES_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DepotSelect);
