import React from 'react';
import { connect } from "react-redux";
import { Row, Col } from 'react-bootstrap';
import { Navigate } from 'react-router-dom';
import Layout from '../utils/layout';
import {
    GET_DEPOT_PRODUCT_MONTH_ACTION,
    GET_DEPOT_SERIES_ACTION,
    GET_DEPOT_PRODUCT_SERIES_ACTION,
    GET_DEPOT_CUSTOMER_ACTION,
    UPDATE_DEPOT_ACTION,
} from "../../redux/actions/actions"

import SpinnerComponent from '../utils/spinner';
import DepotProductMonth from './charts/depot_product_month';
import DepotProductMonthSelect from '../utils/select/depot_product_month';
import DepotSelect from '../utils/select/depot_select';
import DepotSeriesChart from './charts/depot_series';
import DepotProductSeriesChart from './charts/depot_product_series';
import DepotCustomers from './customers';
import DepotCustomerSelect from '../utils/select/depot_customer_select';
import InputComponent from "../home/input_section"

class DepotsPage extends React.Component {
    state = {
        redirect: false,
        spinner: false,
        didnt_load: false,


    }

    static getDerivedStateFromProps = async (props, state) => {
        const { token, depot_product_month, depot_series, depot_product_month_year, depot_product_series, depot_customer } = props

        if (token === undefined || token === "") {
            state.redirect = true
            return state
        }
        else if (depot_product_month && depot_series && depot_product_series && depot_customer) {

            state.spinner = false
        }
        else {
            state.spinner = true
        }
        return state
    }

    componentDidMount = async () => {
        this.setState({ spinner: true })
        let id = this.props.depot !== undefined ? this.props.depot_list[parseInt(this.props.depot)].id : this.props.depot_list[0].id
        let idx = this.props.depot !== undefined ? this.props.depot : 0

        await this.props.UPDATE_DEPOT_ACTION(idx)
        await this.props.GET_DEPOT_PRODUCT_MONTH_ACTION(this.props.depot_product_month_year)

        await this.props.GET_DEPOT_SERIES_ACTION(id, this.props.selection_dates.startDate, this.props.selection_dates.endDate)
        await this.props.GET_DEPOT_PRODUCT_SERIES_ACTION(id, this.props.selection_dates.startDate, this.props.selection_dates.endDate)
        await this.props.GET_DEPOT_CUSTOMER_ACTION(id, this.props.depot_customer_year, this.props.depot_customer_month)

    }

    render() {
        const { spinner } = this.state

        if (this.state.redirect) {
            return <Navigate to={"/login"} />
        }
        return (
            <Layout header_title={"DEPOTS"}>
                {spinner ? <SpinnerComponent spinner={spinner} /> :
                    <div>
                        {this.props.token.is_staff ?
                            <Row>
                                <Col>

                                    <div className='chart-area'>
                                        <DepotProductMonthSelect />
                                        <DepotProductMonth
                                            depot_product_month={this.props.depot_product_month}
                                            depot_list={this.props.depot_list}
                                            depot_product_month_month={this.props.depot_product_month_month}
                                            depot_product_month_year={this.props.depot_product_month_year}
                                            dashboard_metric={this.props.dashboard_metric}
                                            currency_type={this.props.currency_type}
                                            update_depot_product_month={this.props.update_depot_product_month}
                                        />
                                    </div>
                                </Col>
                            </Row> : <InputComponent />}
                        <Row>
                            <Col>
                                <DepotSelect />
                            </Col>
                        </Row>
                        <Row>
                            <Col>
                                <div className='chart-area'>
                                    <DepotSeriesChart
                                        depot_series={this.props.depot_series}
                                        depot={this.props.depot}
                                        dashboard_metric={this.props.dashboard_metric}
                                        currency_type={this.props.currency_type}
                                        selection_dates={this.props.selection_dates}
                                        update_depot_series={this.props.update_depot_series}
                                        update_product_series={this.props.update_product_series}

                                    />
                                </div>
                            </Col>
                        </Row>

                        <Row>
                            <Col xs={7}>
                                <div className='chart-area'>
                                    <DepotProductSeriesChart
                                        depot_product_series={this.props.depot_product_series}
                                        depot={this.props.depot}
                                        dashboard_metric={this.props.dashboard_metric}
                                        currency_type={this.props.currency_type}
                                        selection_dates={this.props.selection_dates}
                                        update_depot_series={this.props.update_depot_series}
                                        update_product_series={this.props.update_product_series}
                                    />
                                </div>
                            </Col>
                            <Col xs={5}>
                                <div className='chart-area'>
                                    <DepotCustomerSelect />
                                    <DepotCustomers
                                        depot_customer={this.props.depot_customer}
                                        depot_customer_year={this.props.depot_customer_year}
                                        depot_customer_month={this.props.depot_customer_month}
                                        depot={this.props.depot}
                                        currency_type={this.props.currency_type}
                                        update_depot_series={this.props.update_depot_series}
                                        update_depot_customer={this.props.update_depot_customer}
                                    />
                                </div>
                            </Col>
                        </Row>
                    </div>
                }
            </Layout>
        )
    }
}

const mapStateToProps = state => {
    return {
        token: state.auth_reducer.token,
        depot_series: state.list_reducer.depot_series,
        depot_product_series: state.list_reducer.depot_product_series,
        depot: state.update_reducer.depot,
        depot_customer: state.list_reducer.depot_customer,
        depot_product_month: state.list_reducer.depot_product_month ? state.list_reducer.depot_product_month : [],
        depot_list: state.list_reducer.depot_list ? state.list_reducer.depot_list : [],
        depot_product_month_month: state.update_reducer.depot_product_month_month,
        depot_product_month_year: state.update_reducer.depot_product_month_year,
        dashboard_metric: state.update_reducer.dashboard_metric,
        currency_type: state.update_reducer.currency_type,
        selection_dates: state.update_reducer.selection_dates,
        depot_customer_year: state.update_reducer.depot_customer_year,
        depot_customer_month: state.update_reducer.depot_customer_month,
        update_depot_product_month: state.list_reducer.update_depot_product_month,
        update_depot_series: state.list_reducer.update_depot_series,
        update_product_series: state.list_reducer.update_product_series,
        update_depot_customer: state.list_reducer.update_depot_customer,
    };
};

const mapDispatchToProps = {
    GET_DEPOT_PRODUCT_MONTH_ACTION,
    GET_DEPOT_SERIES_ACTION,
    GET_DEPOT_PRODUCT_SERIES_ACTION,
    GET_DEPOT_CUSTOMER_ACTION,
    UPDATE_DEPOT_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DepotsPage);
