import React from "react";
import { Form } from "react-bootstrap"
import { connect } from "react-redux"
import {
    UPDATE_DEPOT_CUSTOMER_MONTH_ACTION,
    UPDATE_DEPOT_CUSTOMER_YEAR_ACTION,
    GET_DEPOT_CUSTOMER_ACTION, UPDATE_DEPOT_CUSTOMER_ACTION,
} from "../../../redux/actions/actions"


class DepotCustomerSelect extends React.Component {
    state = {
        year: this.props.depot_customer_year,
        month: this.props.depot_customer_month,
    }

    static getDerivedStateFromProps = (props, state) => {

        state.year = props.depot_customer_year
        state.month = props.depot_customer_month
        return state
    }

    handleChange = async (year, month) => {
        let id = this.props.depot_list[this.props.depot].id
        await this.props.UPDATE_DEPOT_CUSTOMER_ACTION()
        await this.props.UPDATE_DEPOT_CUSTOMER_YEAR_ACTION(year)
        await this.props.UPDATE_DEPOT_CUSTOMER_MONTH_ACTION(month)
        await this.props.GET_DEPOT_CUSTOMER_ACTION(id, year, month)
    }

    render() {
        const years = [2022, 2021]
        const { year, month } = this.state
        const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"]
        return (
            <div className="pie-select">
                <Form.Group className="mb-4 pie-select__child" controlId="exampleForm.ControlInput1">
                    <Form.Label>Year</Form.Label>
                    <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleChange(e.target.value, month)}>
                        <option>{this.state.year}</option>
                        {years.map((year, idx) =>
                            parseInt(this.state.year) === year ? null : <option key={idx} value={year}>{year}</option>
                        )}

                    </Form.Select>
                </Form.Group>

                <Form.Group className="mb-4 pie-select__child" controlId="exampleForm.ControlInput1">
                    <Form.Label>Month</Form.Label>
                    <Form.Select aria-label="Default select example" size='sm' onChange={(e) => this.handleChange(year, e.target.value)}>
                        {parseInt(this.state.month) === 13 ? null : <option value={this.state.month}>{months[parseInt(this.state.month)]}</option>}
                        <option value={13}>All</option>
                        {months.map((month, idx) =>
                            parseInt(this.state.month) === idx ? null : <option key={idx} value={idx}>{month}</option>
                        )}

                    </Form.Select>
                </Form.Group>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        depot_customer_month: parseInt(state.update_reducer.depot_customer_month),
        depot_customer_year: parseInt(state.update_reducer.depot_customer_year),
        depot: parseInt(state.update_reducer.depot),
        depot_list: state.list_reducer.depot_list,
    };
};

const mapDispatchToProps = {
    UPDATE_DEPOT_CUSTOMER_MONTH_ACTION,
    UPDATE_DEPOT_CUSTOMER_YEAR_ACTION,
    GET_DEPOT_CUSTOMER_ACTION,
    UPDATE_DEPOT_CUSTOMER_ACTION,
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DepotCustomerSelect);
